import { createStore } from 'vuex'

import {all_cards} from "@/js/cards_list";
import {easy_cards} from "@/js/cards_list";
import {shuffle} from "@/js/helpers";

const cardsKey = 'cards';
const initCardsList = (key, cards) => {
    const stored = localStorage.getItem(key);
    if (stored && stored.length > 0) {
        return JSON.parse(stored);
    }
    let result = shuffle([...cards]);
    localStorage.setItem(key, JSON.stringify(result));
    return result;
}

export const store = createStore({
    strict: true,
    state () {
        return {
            cards_list: initCardsList(cardsKey, all_cards),
            index: JSON.parse(localStorage.getItem('index')) || 0,
        }
    },
    getters: {
        currentCard (state) {
            return state.cards_list[state.index];
        },
        currentImgSrc (state) {
            return `img/curated-flashcards/${state.cards_list[state.index]}.jpg`
        },
        nextImgSrc (state) {
            return `img/curated-flashcards/${state.cards_list[state.index+1]}.jpg`
        }
    },
    mutations: {
        advanceIndex (state) {
            state.index++;
            if (state.index >= state.cards_list.length) {
                console.log(`Index reached ${state.index}, shuffling`);
                shuffle(state.cards_list);
                localStorage.setItem(cardsKey, JSON.stringify(state.cards_list));
                state.index = 0;
            }
            localStorage.setItem('index', JSON.stringify(state.index));
        },
        prevIndex (state) {
            if (state.index <= 0) {
                return
            }
            state.index--;
            localStorage.setItem('index', JSON.stringify(state.index));
        },
    },
    actions: {
    }
})
