<script setup>

import {store} from "@/store/store";
import 'animate.css';
import {onMounted, ref} from "vue";
const gameImg = ref(null);

const isAnimationRunning = ref(true);

const advance = () => {
  isAnimationRunning.value = true;
  store.commit('advanceIndex');
  gameImg.value.classList.add('animate__jackInTheBox');
  setTimeout(() => {
    gameImg.value.classList.remove('animate__jackInTheBox');
    isAnimationRunning.value = false;
  }, 800);
}

onMounted(() => {
  setTimeout(() => {
    gameImg.value.classList.remove('animate__zoomIn');
    isAnimationRunning.value = false;
  }, 1000);
})

</script>

<template>
  <div class="text-neutral-content px-2">
    Explain the meaning of this word:
  </div>
  <div class="flex justify-center">
    <img
        ref="gameImg"
        class="game-img object-contain animate__animated animate__fast animate__zoomIn"
        :src="store.getters.currentImgSrc"
        alt="">
  </div>
  <div class="flex justify-stretch mt-6 p-2">
    <button
        :disabled="isAnimationRunning"
        class="btn btn-outline flex-shrink-0 text-neutral-content" @click="store.commit('prevIndex')">
      ←
    </button>
    <button
        :disabled="isAnimationRunning"
        class="btn btn-outline flex-grow ml-1 text-neutral-content" @click="advance">
      →
    </button>
  </div>
</template>

<style scoped>
.game-img {
  width: 100%;
  max-height: 70vh;
}
</style>